/*Pad.css*/
.drum-pad {
	height: 80px;
	width: 80px;
	border-radius: 8px;
	border: none;
	background: -webkit-radial-gradient(center, ellipse, #1e5799 0%,#cdd2d6 0%,#0b87e5 100%);
	background: radial-gradient(ellipse at center, #1e5799 0%,#cdd2d6 0%,#0b87e5 100%);
}
.drum-pad:hover {
	cursor: pointer;
	background: -webkit-radial-gradient(center, ellipse, #73A8E5 0%,#cdd2d6 0%,#cdd2d6 40%,#73A8E5 100%);
	background: radial-gradient(ellipse at center, #73A8E5 0%,#cdd2d6 0%,#cdd2d6 40%,#73A8E5 100%);
}
.drum-pad.active {
	background: -webkit-radial-gradient(center, ellipse, #73A8E5 0%,#cdd2d6 0%,#cdd2d6 40%,#73A8E5 100%);
	background: radial-gradient(ellipse at center, #73A8E5 0%,#cdd2d6 0%,#cdd2d6 40%,#73A8E5 100%);
}
.pad-display {
	font-family: Barlowe, sans-serif;
}
.pad-display.hidden {
	opacity: 0;
}
/*QueuePad.css*/
.queue-pad {
	height: 80px;
	width: 80px;
	border-radius: 8px;
	border: none;
	background: -webkit-radial-gradient(center, ellipse, #1e5799 0%,#cdd2d6 0%,#0b87e5 100%);
	background: radial-gradient(ellipse at center, #1e5799 0%,#cdd2d6 0%,#0b87e5 100%);
	font-family: Barlowe, sans-serif;
}
.queue-pad:hover {
	cursor: pointer;
}
.queue-pad.active {
	border: 2px solid black;
}
.queue-pad:active {
	border: 2px solid black;
}
.queue-pad.has-sound {
	background: -webkit-radial-gradient(center, ellipse, #73A8E5 0%,#cdd2d6 0%,#cdd2d6 40%,#73A8E5 100%);
	background: radial-gradient(ellipse at center, #73A8E5 0%,#cdd2d6 0%,#cdd2d6 40%,#73A8E5 100%);
}
.drum-machine {
	background-color: #2d3436;
	border-radius: 2px;
	width: 304px;
}
.display {
	height: 40px;
	padding: 20px;
	display: flex;
}
.display > div {
	margin: auto;
	background-color: #FFF;
	width: 50%;
	height: 50px;
	text-align: center;
}
.display > div > p {
	font-family: Barlow, sans-serif;
	font-weight: 700;
}
.pad-wrapper {
	display: grid;
	grid-template-columns: 80px 80px 80px;
	grid-template-rows: 80px 80px 80px;
	grid-gap: 12px;
	padding: 20px;
}
.queue-wrapper {
	display: grid;
	grid-template-columns: repeat(4, 88px);
	grid-template-rows: repeat(4, 88px);
}
button {
	padding: 12px 20px;
	border: none;
	border-radius: 2px;
	background-color: #1e5799;
	color: #fff;
	font-family: inherit;
	font-size: 1.2rem;
}
button:hover {
	cursor: pointer;
	background-color: #73A8E5;
}
.tempo-wrapper {
	display: grid;
	grid-template-columns: auto auto;
	grid-template-row: auto auto;
}
.tempo-wrapper > button {
	padding: 0px;
	grid-column-start: 1;
}
.tempo-wrapper > p {
	grid-column: 2 / 2;
	grid-row: 1 / 3;
}
